/* app/page.module.css - ESTILOS COMPLETOS Y FINALES */

/* --- DECLARACIONES DE VARIALBES Y BASE (Asumimos que estÃ¡n en globals.css) --- */

.main {
    max-width: 100%; 
    margin: 0 auto;
    background-color: var(--color-espn-dark); 
}

/* ------------------------------------- */
/* 1. ESTILOS DE LA PÃGINA DE PORTADA (/) */
/* ------------------------------------- */

.visualHeader {
    display: flex;
    flex-direction: column;
    min-height: 100vh; 
    position: relative;
    overflow: hidden;
    color: var(--color-espn-white);
    
    /* FONDO PRINCIPAL: portada.png */
    background-image: url('/images/portada.png'); 
    background-size: cover;
    background-position: center;
}

.visualHeader::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    /* Filtro oscuro para contraste del texto */
    background-color: rgba(0, 0, 0, 0.4); 
    z-index: 1; 
}

.headerTitleContainer {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 10;
    text-align: center;
    width: 90%;
    max-width: 800px;
}

.headerTitle {
    font-size: 4.5em; 
    font-family: var(--font-heading);
    margin-bottom: var(--spacing-sm);
    text-shadow: 0 4px 8px rgba(0, 0, 0, 0.7);
}

.headerSubtitle {
    font-size: 1.15em;
    font-weight: 300;
    color: var(--color-espn-light-gray);
    text-shadow: 0 2px 4px rgba(0, 0, 0, 0.7);
}

/* --- CONTENEDOR DE SELECTOR DE DEPORTE (3 COLUMNAS INTERACTIVAS) --- */
.sportSelectorContainer {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    display: flex;
    height: 30vh; 
    z-index: 5;
    background-color: transparent; 
}

.sportSelectorItem {
    flex: 1;
    position: relative;
    overflow: hidden;
    cursor: pointer;
    transition: all 0.3s ease;
    
    /* Ajuste para hitbox completo */
    display: flex;
    height: 100%; 
    
    justify-content: center;
    align-items: center; 
    padding: var(--spacing-lg);
    border-right: 1px solid rgba(255, 255, 255, 0.2); 
    background: linear-gradient(to top, rgba(0, 0, 0, 0.7) 0%, rgba(0, 0, 0, 0.1) 100%);
}
.sportSelectorItem:last-child {
    border-right: none;
}

.sportSelectorItem::before {
    content: none;
}

/* EFECTO HOVER */
.sportSelectorItem:hover {
    background: linear-gradient(to top, rgba(0, 0, 0, 0.85) 0%, rgba(0, 0, 0, 0.3) 100%);
}

/* EFECTO ACTIVO */
.activeSelector {
    box-shadow: inset 0 -5px 0 var(--color-espn-red); 
    filter: brightness(1.2); 
}

.sportButtonText {
    position: relative;
    z-index: 3; 
    font-family: var(--font-body);
    font-size: 1.5em;
    font-weight: bold;
    color: var(--color-espn-white);
    text-shadow: 0 2px 4px rgba(0, 0, 0, 0.9);
    background-color: transparent; 
    padding: 5px 10px;
    text-transform: uppercase;
}

/* --- MENSAJE DE BIENVENIDA (Solo aparece en la home si no hay state) --- */
.dynamicContent {
    padding: 0; 
    min-height: 400px;
}

.welcomeMessage {
    text-align: center;
    padding: 80px 20px;
    background-color: var(--color-espn-dark);
    color: var(--color-espn-white);
}

.welcomeButton {
    background-color: var(--color-espn-red);
    color: var(--color-espn-white);
    padding: 12px 30px;
    margin-top: var(--spacing-lg);
    display: inline-block;
    border-radius: 3px;
    font-weight: bold;
    text-transform: uppercase;
    transition: background-color 0.3s;
}
.welcomeButton:hover {
    background-color: #ff3333;
}


/* ------------------------------------- */
/* 2. ESTILOS DE LA PÃGINA DE DEPORTE ([sportName]/page.js) */
/* ------------------------------------- */

.whiteContentWrapper {
    background-color: var(--color-espn-white); /* Fondo blanco para las pÃ¡ginas de deporte */
    color: var(--color-espn-dark); /* Texto oscuro sobre blanco */
    min-height: 100vh;
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.2);
}

.sportContentArea {
    padding: var(--spacing-lg) var(--spacing-xl);
}

.sportContentTitle {
    color: var(--color-espn-dark);
    text-align: center;
    margin-bottom: var(--spacing-lg);
}

/* --- ENCABEZADO SUPERIOR (Para la pÃ¡gina de deporte) --- */
.secondaryHeader {
    background-color: var(--color-espn-dark); 
    color: var(--color-espn-white);
    padding: var(--spacing-sm) var(--spacing-xl);
    border-bottom: 1px solid var(--color-espn-gray);
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.secondaryHeader h1 {
    font-size: 2em;
    margin: var(--spacing-sm) 0 0 0;
}

.backButton {
    font-weight: bold;
    color: var(--color-espn-light-gray);
    float: left;
    transition: color 0.3s;
}
.backButton:hover {
    color: var(--color-espn-red);
}

/* --- SUB-NAVEGACIÃ“N (Barra estilo ESPN) --- */
.subNavContainer {
    border-bottom: 2px solid var(--color-espn-red); 
    background-color: var(--color-espn-white);
    padding-top: var(--spacing-md);
}

.subNavHeader {
    display: flex;
    align-items: center;
    padding: 0 var(--spacing-xl) var(--spacing-md) var(--spacing-xl);
    border-bottom: 1px solid var(--color-espn-light-gray);
}

.subNavIcon {
    font-size: 2em; 
    color: var(--color-espn-red);
    margin-right: var(--spacing-sm);
}

.subNavSportName {
    font-family: var(--font-heading);
    font-size: 1.8em;
    color: var(--color-espn-dark);
}

.subNavLinks {
    display: flex;
    gap: var(--spacing-lg);
    padding: 0 var(--spacing-xl);
    overflow-x: auto; 
}

/* ðŸ’¡ CONTENEDOR DEL ÃTEM DE NAVEGACIÃ“N (Clave para el Dropdown) */
.navItemWrapper {
    position: relative; 
    height: 100%; 
    display: flex;
    align-items: center;
}

.navItem {
    color: var(--color-espn-dark);
    font-weight: 500;
    padding: var(--spacing-md) 0;
    transition: color 0.3s;
    white-space: nowrap; 
    border-bottom: 3px solid transparent;
    cursor: pointer; 
}

.navItem:hover {
    color: var(--color-espn-red);
}

.activeNavItem {
    color: var(--color-espn-red);
    border-bottom: 3px solid var(--color-espn-red); 
    font-weight: 700;
}

/* ðŸ’¡ ESTILO DEL DROPDOWN MENU */
.dropdownMenu {
    position: absolute;
    top: 100%; 
    left: 50%;
    transform: translateX(-50%); 
    
    background-color: var(--color-espn-white);
    border: 1px solid var(--color-espn-light-gray);
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
    z-index: 50; 
    min-width: 250px;
    padding: var(--spacing-sm) 0;
    border-radius: 4px;
    margin-top: 2px; 
}

.dropdownItem {
    display: block;
    padding: var(--spacing-sm) var(--spacing-md);
    color: var(--color-espn-dark);
    text-decoration: none;
    white-space: nowrap;
    transition: background-color 0.2s, color 0.2s;
}

.dropdownItem:hover {
    background-color: var(--color-espn-red);
    color: var(--color-espn-white);
}


/* --- SECCIÃ“N DE DETALLES DEL DEPORTE (Plantilla) --- */

.sportSection {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 60px 40px;
    min-height: 300px; 
    color: var(--color-espn-white);
    border-bottom: 1px solid var(--color-espn-gray); 
}

/* Clase especial para cuando el SportSection tiene fondo claro */
.sportSectionWhite {
    background-color: var(--color-espn-light-gray); 
    color: var(--color-espn-dark); 
    padding: 40px 20px;
    border-bottom: none; 
}

.sportDetails {
    padding: var(--spacing-xl) 40px;
    background-color: var(--color-espn-white);
    color: var(--color-espn-dark);
    min-height: 400px;
}

.sportDetails h3 {
    color: var(--color-espn-dark);
    text-transform: none;
    font-family: var(--font-body);
}

/* --- ESTILOS DE FONDO POR DEPORTE --- */
.sportFutbol { background-color: var(--color-sport-futbol); }
.sportVoleibol { background-color: var(--color-sport-voleibol); }
.sportBasquetbol { background-color: var(--color-sport-basquetbol); }


/* --- ELEMENTOS DE LA SECCIÃ“N (IMAGEN, BOTONES, PUNTOS) --- */
.courtContainer {
    flex-grow: 1; 
    text-align: center;
    padding: 0 var(--spacing-xl); 
}

.courtImage {
    max-width: 100%; 
    height: auto; 
    display: block; 
    margin: 0 auto var(--spacing-lg) auto; 
    border-radius: 5px; 
    box-shadow: 0 8px 15px rgba(0, 0, 0, 0.4);
    border: 3px solid var(--color-espn-red); 
}

.matchesButton {
    padding: 15px 40px;
    border-radius: 5px;
    color: var(--color-espn-white);
    font-weight: 700;
    font-family: var(--font-heading);
    display: inline-block;
    transition: background-color 0.3s, transform 0.2s, box-shadow 0.3s;
    text-transform: uppercase;
    letter-spacing: 1px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.4);
    background-color: var(--color-espn-red); 
}

.matchesButton:hover {
    background-color: var(--color-espn-white);
    color: var(--color-espn-dark);
    transform: translateY(-3px);
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.5);
}

.pointsContainer {
    display: flex;
    flex-direction: column;
    gap: var(--spacing-md);
}

.point {
    width: 25px; 
    height: 25px;
    border-radius: 50%;
    box-shadow: inset 0 0 8px rgba(0, 0, 0, 0.5), 0 0 10px rgba(255, 255, 255, 0.2); 
    border: 2px solid var(--color-espn-white);
}

/* Estilos de puntos en la secciÃ³n blanca */
.sportSectionWhite .point {
    background-color: var(--color-espn-gray);
    border-color: var(--color-espn-dark);
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
}

.sportFutbol .point { background-color: #3cb371; } 
.sportVoleibol .point { background-color: #4682b4; } 
.sportBasquetbol .point { background-color: #ff8c00; } 


/* --- RESPONSIVE --- */
@media (max-width: 768px) {
    .visualHeader { min-height: 80vh; }
    .headerTitle { font-size: 3em; }
    .sportSelectorContainer { height: 20vh; }
    .sportSelectorItem { padding: var(--spacing-md); }
    .sportButtonText { font-size: 1.1em; }
    .sportSection { padding: 40px 15px; }
    .sportSectionWhite { padding: 40px 15px; }
    
    .sportSection, .sportSectionWhite { flex-direction: column; gap: var(--spacing-lg); }
    .pointsContainer { flex-direction: row; gap: var(--spacing-sm); padding: var(--spacing-md) 0; }
    .sportSection > :first-child, .sportSectionWhite > :first-child { order: 1; }
    .courtContainer { order: 2; padding: var(--spacing-md) 0; }
    .sportSection > :last-child, .sportSectionWhite > :last-child { order: 3; }

    .dropdownMenu {
        left: 0;
        transform: translateX(0);
    }
}
@media (max-width: 480px) {
    .visualHeader { min-height: 60vh; }
    .headerTitle { font-size: 2.5em; }
    .sportSelectorContainer {
        height: 15vh;
        flex-direction: column; 
    }
    .sportSelectorItem {
        border-right: none;
        border-bottom: 1px solid rgba(255, 255, 255, 0.1);
        height: 100%;
        align-items: center;
    }
    .sportButtonText {
        background-color: transparent;
        font-size: 1.2em;
        text-shadow: 0 2px 4px rgba(0, 0, 0, 0.9);
    }

    /* app/page.module.css (NUEVOS ESTILOS PARA LA LISTA DE EQUIPOS) */

.teamsListContainer {
    max-width: 800px;
    margin: 0 auto;
    padding: var(--spacing-lg) 0;
}

.teamsList {
    list-style: none;
    padding: 0;
}

.teamItem {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: var(--spacing-md) var(--spacing-sm);
    border-bottom: 1px solid var(--color-espn-light-gray);
    transition: background-color 0.2s;
    font-size: 1.1em;
}

.teamItem:last-child {
    border-bottom: none;
}

.teamItem:hover {
    background-color: var(--color-espn-light-gray); 
}

.teamName {
    font-weight: bold;
    color: var(--color-espn-dark);
}

.teamDetail {
    color: var(--color-espn-gray);
    font-size: 0.9em;
}
    
}